import { Pixel } from '../Pixel';

function pixelToStr(pix: Pixel): string{
    switch(pix){
        case Pixel.Black:
            return '0';
        case Pixel.White:
            return '-';
        case Pixel.Unknown:
            return 'x';
    }
}
function strToPixel(string: string): Pixel{
    switch(string){
        case '0':
            return Pixel.Black;
        case '-':
            return Pixel.White;
        case 'x':
            return Pixel.Unknown;
        default:
            throw `Error: invalid pixel string value: "${string}"`;
    }
}


function halfNibblesToByte(numbers: number[]): number {
    if(numbers.length !== 4) throw "Error: not enough nibbles for a byte";
    return (numbers[0] +
            (numbers[1] >> 2) +
            (numbers[2] >> 4) +
            (numbers[3] >> 5));
}

function encodedGridCharacterToNumber(str: string): number{
    switch(str) {
        case '0': return 0;
        case '-': return 1;
        case 'x': return 2;
        case ',': return 3;
        default: throw "invalid grid encoded number";
    }
}

export function serializeGrid(gridData: Pixel[][]): string {
    var outputObject = gridData.map(column => 
            column.map(pixel => pixelToStr(pixel)).join('')
            ).join(',');
    return outputObject;
}

export function deserializeGrid(gridString: string): Pixel[][] {
    const columns = gridString.split(',');
    const grid = columns.map(column => column.split('').map(char => strToPixel(char)));
    return grid;
}


